@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-300.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-300.woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-regular.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-regular.woff"); }

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-600.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-600.woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/open-sans-v17-latin_cyrillic-700.woff2"), url("../fonts/open-sans-v17-latin_cyrillic-700.woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  position: relative;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: black;
  background-color: white; }

a {
  text-decoration: none; }

img {
  border-style: none;
  max-width: 100%; }

@media (max-width: 767px) {
  .wrapper {
    min-width: 320px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .wrapper {
    max-width: 768px; } }

@media (min-width: 1024px) {
  .wrapper {
    max-width: 1024px; } }
